.second-banner{
    margin:  50px 0 0 0;
    .slide1, .slide2{
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        min-height: 100vh;
    }

    .masking{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        background-image: linear-gradient(to right, #000 , transparent);
        width: 50%;
    }
    .carousel-caption {
        transform: translateY(-50%);
        bottom: 0;
        top: 50%;
        text-align: left;
        text-shadow: 1px 1px 4px rgba(0,0,0, 0.5);
        h2{
            margin: 0;
            font-size: 40px;
            font-weight: 500;
            text-align: left;
        }

        p{
            margin-bottom: 45px;
            line-height: 24px;
            font-size: 18px;
            font-weight: bold;
        }
    }

    .carousel-indicators{
        margin-right: 0;
        margin-bottom: 65px;
        li{
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 15px;
            width: 90px;
            height: 130px;
            border-radius: 10px!important;
            border: none;
            background-size: cover;
            text-indent: 0%;
            text-align: center;
            opacity: 10;
            img{
                width: 48px;
            }
            p{
                margin: 0;
                // display: flex;
                // justify-content: center;
                // align-items: center;
                // margin: 30px 0 0 0;
                // height: 60px;
                font-size: 12px;
                font-weight: 700;
                line-height: normal;
                color: #fff;
                text-shadow: 1px 1px 1px rgba(0, 0, 0, 1);
            }
            &:hover{
                opacity: 10;
            }
        }
        
        .active{
            margin-top: -30px;
            width: 120px;
            height: 160px;
        }
    }

    .frame-top{
        position: absolute;
        top: -2px;
        z-index: 1;
        background-image: url(../images/frame-top.svg);
        background-repeat: no-repeat;
        background-position: center top;
        background-size: contain;
        width: 100%;
        height: 195px;
    }
      
}

